import React, { useState, useRef, useEffect } from "react";
import { Col, Row, Container, Button } from "react-bootstrap";
import { navigate } from "gatsby";

import Layout from "../components/layout.js";
import Seo from "../components/seo";
import Keyboards from "../components/Keyboards.js";
import "../components/pages-css/sCita.css";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Timer from "../components/timer.js";
import api from "../components/actions/riveroapi";

export default function DatosActuales() {

  const [show, setShow] = useState(false);
  const [modal, setModal] = useState(false);
  const [error, setError] = useState(0);
  const [click, setClick] = useState(false);

  const [pruebaDatos, setPruebaDatos] = useState({
    nombre: "",
    correo: "",
    telefono: ""
  })

  const refForm = useRef();

  useEffect(() => {

    if (typeof window !== 'undefined') {

      var jParse = JSON.parse(localStorage.getItem("informacion"));
      //console.log("Informacion: ", jParse);

      var correo = jParse.contactoCliente.email;
      var nombre = jParse.datosCliente.fullName;
      var numero = jParse.contactoCliente.mobile;

      let replaceEmail = correo.replace(/(\w{4}).*?@/, '$1****@');

      let replaceName = nombre.replace(/,/g, " ");

      let splitName = replaceName.split(/[" "]+/);
    
      const mapita = splitName.map((obj) => {
          let asteriskName = obj.slice(0, 3);
          let nuevaName = `${asteriskName}******`;
          return nuevaName;
      })
    
      let joinName = mapita.join(" ");


    let replacePhone = numero.replace(/^(\d{6})|\d?\d?(\d{4}$)|(\d)/, '****');

      setPruebaDatos({
        ...pruebaDatos,
        correo: replaceEmail,
        nombre: joinName,
        telefono: replacePhone
      })

  } else {
      //console.log('we are running on the server');
  }

  }, []);

  function continuar (){

    var datosJ = JSON.parse(localStorage.getItem("informacion"));
    //console.log("Me han pulsado jiji", datosJ);

    const params = {
      envio: "envio codigo",
      contacto: datosJ.contactoCliente.email,
      medio: "correo"
    }

    api.CodigosVerficiacion(params).then((respCo) => {}).catch((error) => console.log("Error, ", error))

    navigate("/confirmacion");
  }

  return (
    <Layout backUrl={"/kilometraje"}>
      <Seo title="Auto servicio" />
        <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
        {/*<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>*/}
            <Row className="row-cita-arriba">
                <div className="container-cita">
                  <Row>
                    <Col className="titulo-cita"><h1 className="titulo-h1">Datos actuales</h1></Col>
                  </Row>
                  <Row>
                    <Col className="titulo-cita"><p className="texto-gris">Te enviaremos un código de verificación.</p></Col>
                  </Row>
                  <Row className="row-cita-abajo" style={{marginBottom: 0}}>
                      <Col lg={9}>
                        <p className="texto-negro">Nombre: </p>
                            <input
                                disabled
                                autoComplete="off"
                                type="text"
                                id="inputContacto"
                                className="form-control"
                                placeholder={pruebaDatos.nombre}
                            />
                      </Col>
                      <Col lg={9}>
                        <p className="texto-negro">Correo: </p>
                            <input
                                disabled
                                autoComplete="off"
                                type="text"
                                id="inputContacto"
                                className="form-control"
                                placeholder={pruebaDatos.correo}
                            />
                      </Col>
                      <Col lg={9}>
                        <p className="texto-negro">Telefono: </p>
                            <input
                                disabled
                                autoComplete="off"
                                type="text"
                                id="inputContacto"
                                className="form-control"
                                placeholder={pruebaDatos.telefono}
                            />
                      </Col>
                  </Row>
                  <Row style={{height: '10%'}} className="titulo-cita">
                    <Col lg={9} className="minititulo-cita">
                        <p style={{marginBottom: 0}} className="texto-gris">Enviar mi código de verificación a: </p>
                    </Col>
                  </Row>
                  <Row >
                    <Col className="titulo-cita">
                        <Button onClick={() => continuar()} className="btn-celeste">Correo electrónico</Button>
                    </Col>
                  </Row>
                </div>
            </Row>
            <Row className="row-cita-abajo">
              <VideoSeccion refForm={refForm} none='none'/>
            </Row>
        </Container>
        <ModalEmergente modal={modal} setModal={setModal} error={error} setClick={setClick}/>
    </Layout>
  )
}
